/**
 * 
 * @returns {Promise} promise.then(position=>{})
 */
export default function getLocation() {
    return new Promise(function (resolve, reject) {
        /**
         * 
         * 
         */
        let AMapGeolocation = ()=> {
            let geolocation
            /**
             * 
             */
            let map = new AMap.Map('container', {resizeEnable: true})
            map.plugin('AMap.Geolocation', function () {
                geolocation = new AMap.Geolocation({
                    //:true
                    enableHighAccuracy: true,
                    //10
                    timeout: 10000,
                    //Pixel(10, 20)
                    buttonOffset: new AMap.Pixel(10, 20),
                    //false
                    zoomToAccuracy: true,
                    buttonPosition: 'RB'
                })
                map.addControl(geolocation)
                geolocation.getCurrentPosition()
                AMap.event.addListener(geolocation, 'complete', onComplete)//
                AMap.event.addListener(geolocation, 'error', onError)//
            })

            /**
             * 
             * @param data
             */
            function onComplete(data) {
                console.log('[Leo] => ', data.position.getLng(), data.position.getLat())
                resolve([data.position.getLng(), data.position.getLat()])
            }

            /**
             * 
             * @param data
             */
            function onError(data) {
                console.log('[Leo] => ')
                reject(new Error(''))
            }
        }

        /**
         * H5
         */
        if (navigator.geolocation) {//H5
            navigator.geolocation.getCurrentPosition(
                /**
                 * H5
                 * @param position 
                 */
                function (position) {
                    let longitude = position.coords.longitude//
                    let latitude = position.coords.latitude//
                    //console.log('[Leo] => ', position)
                    resolve([longitude, latitude])
                },
                /**
                 * H5
                 * @param error
                 */
                function (error) {
                    //
                    AMapGeolocation()
                    //H5
                    //switch (error.code) {
                    //    case 1:
                    //        console.error('[Leo] => ')
                    //        reject(new Error(''))
                    //        break
                    //    case 2:
                    //        console.error('[Leo] => ')
                    //        reject(new Error(''))
                    //        break
                    //    case 3:
                    //        console.error('[Leo] => ')
                    //        reject(new Error(''))
                    //        break
                    //    case 4:
                    //        console.error('[Leo] => ')
                    //        reject(new Error(''))
                    //        break
                    //}
                }, {
                    enableHighAccuracy: true,
                    maximumAge: 1000
                }
            )
        } else {//H5
            console.error('[Leo] => geolocation')
            reject(new Error('geolocation'))
        }
    })
}



