import Vue from "vue";
import Vuex from "vuex";
import * as actions from './actions'
import * as getters from './getters'
import base from './modules/base'
//import createLogger from 'vuex/dist/logger' //vuexLogger
//const debug = process.env.NODE_ENV !== 'production' // Webpack  DefinePlugin  process.env.NODE_ENV !== 'production'  false

Vue.use(Vuex)

export default new Vuex.Store({
    actions,
    getters,
    modules: {
        base
    },
    //strict: debug,//TODO:
    //plugins: debug ? [createLogger()] : []//vuex
})

